export default{
	// isShow ? 'show' : 'hide'
	data() {
		return {
			isShow: false
		}
	},
	props:{
		title: {
			type: String,
			default() {
				return ''
			}
		},
		showNumber: {
			type: String,
			 default() {
				 return 0
			 }
		}
		
	},
	watch: {
		showNumber(newVal) {
			if(newVal === '1'){
				this.isShow = true
			}else {
				
				setTimeout(()=>{
					this.isShow = false
				},500);
			}
		}
	},
	
	methods:{
	}
	
}
